import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function interestBurdenTtm(tableName: string) {
  const title = "Interest Burden (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["interestBurdenTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Portion of earnings kept after interest.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about interest burden or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.interestBurdenTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.interestBurdenTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { interestBurdenTtm }
